
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { PropType, defineComponent } from 'vue'
import { IbUser } from "@/views/cib/cib-interface/ib-subscription-edit.interface"
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const'
import { modalController } from '@ionic/vue';
import { CIB20100339_REQ_BODY, CIB20100339_RES } from '@/shared/type/CIB20100339.type';
import { screenMode } from "@/shared/type/Approval.type";
import { AuthorizationDetail } from '@/shared/type/APM20200113.type';

export default defineComponent({
  name: "cib201003ss10",
  components: {
    VueDatePicker
  },
  props: {
    data: {
      type: Object as PropType<IbUser>,
      required: true
    },
    approvalPayload: {
      type: Object as PropType<AuthorizationDetail>,
      required: false
    },
    currentTab: {
      type: String,
      required: true,
    }
  },

  data() {
    const userID = this.$route.query.userID;
    const customerNo = this.$route.query.customerNo;
    return {
      userID,
      customerNo,
      transferAuthorizationHour: {
        operationServiceCode: "",
        startTime: "",
        endTime: "",
        mondayYN: "",
        tuesdayYN: "",
        wednesdayYN: "",
        thursdayYN: "",
        fridayYN: "",
        saturdayYN: "",
        sundayYN: ""
      },
      isEditable: false,
      isDisable: false,
      isAllowDesignatedHour: false,
      designatedHour: {
        startTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        endTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        transferDay: {
          mondayYN: false,
          tuesdayYN: false,
          wednesdayYN: false,
          thursdayYN: false,
          fridayYN: false,
          saturdayYN: false,
          sundayYN: false
        }
      },
      tempDesignatedHour: {
        startTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        endTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        transferDay: {
          mondayYN: false,
          tuesdayYN: false,
          wednesdayYN: false,
          thursdayYN: false,
          fridayYN: false,
          saturdayYN: false,
          sundayYN: false
        }
      },
      tempOperationCode:'',
      designatedHourTemp: {
        startTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        endTime: { "hours": 0, "minutes": 0, "seconds": 0 },
        transferDay: {
          mondayYN: false,
          tuesdayYN: false,
          wednesdayYN: false,
          thursdayYN: false,
          fridayYN: false,
          saturdayYN: false,
          sundayYN: false
        }
      },
      COMMON_KEY_VALUE,
      operationMode: 'normal' as screenMode,
      isSelectAllDate: false,
      isChangeTransferDay:false,
      isChangeFromStartTime:false,
      transferAuthorizationCode:'',
      isSelect:false,
      isDisableTransferDays:false,
      isSelectAll:'',
      isfirstTransferAuthorization:'',
      isSecondTransferAuthorization:'',
      isthirdTransferAuthorization:'',
      isSecond:'',
    }
  },

  computed: {
    approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},

    formattedStartTime(): string {
        return this.$datetime.formatVueTimePicker(this.designatedHour.startTime);
    },

    formattedEndTime(): string {
        return this.$datetime.formatVueTimePicker(this.designatedHour.endTime);
    },
  },

  watch: {
    data(customerData: IbUser) {
      if(this.approvalType == ''){
      this.customerNo= customerData.userInfo.customerNo,
      this.transferAuthorizationHour.operationServiceCode= customerData.transferAuthorizationHour.operationServiceCode,
      this.transferAuthorizationHour.startTime= customerData.transferAuthorizationHour.operationServiceStartTime,
      this.transferAuthorizationHour.endTime= customerData.transferAuthorizationHour.operationServiceEndTime,
      this.transferAuthorizationHour.mondayYN= customerData.transferAuthorizationHour.operationMondayYN,
      this.transferAuthorizationHour.tuesdayYN= customerData.transferAuthorizationHour.operationTuesdayYN,
      this.transferAuthorizationHour.wednesdayYN= customerData.transferAuthorizationHour.operationWednesdayYN,
      this.transferAuthorizationHour.thursdayYN= customerData.transferAuthorizationHour.operationThursdayYN,
      this.transferAuthorizationHour.fridayYN= customerData.transferAuthorizationHour.operationFridayYN,
      this.transferAuthorizationHour.saturdayYN= customerData.transferAuthorizationHour.operationSaturdayYN,
      this.transferAuthorizationHour.sundayYN= customerData.transferAuthorizationHour.operationSundayYN,

      this.designatedHour.startTime = this.timeFormatter(this.transferAuthorizationHour.startTime)
      this.designatedHour.endTime = this.timeFormatter(this.transferAuthorizationHour.endTime)
      this.designatedHour.transferDay.mondayYN = this.stringToBoolean(this.transferAuthorizationHour.mondayYN)
      this.designatedHour.transferDay.tuesdayYN = this.stringToBoolean(this.transferAuthorizationHour.tuesdayYN)
      this.designatedHour.transferDay.wednesdayYN = this.stringToBoolean(this.transferAuthorizationHour.wednesdayYN)
      this.designatedHour.transferDay.thursdayYN = this.stringToBoolean(this.transferAuthorizationHour.thursdayYN)
      this.designatedHour.transferDay.fridayYN = this.stringToBoolean(this.transferAuthorizationHour.fridayYN)
      this.designatedHour.transferDay.saturdayYN = this.stringToBoolean(this.transferAuthorizationHour.saturdayYN)
      this.designatedHour.transferDay.sundayYN = this.stringToBoolean(this.transferAuthorizationHour.sundayYN)
      
      this.tempOperationCode = customerData.transferAuthorizationHour.operationServiceCode,
      this.tempDesignatedHour.startTime = this.timeFormatter(this.transferAuthorizationHour.startTime)
      this.tempDesignatedHour.endTime = this.timeFormatter(this.transferAuthorizationHour.endTime)
      this.tempDesignatedHour.transferDay.mondayYN = this.stringToBoolean(this.transferAuthorizationHour.mondayYN)
      this.tempDesignatedHour.transferDay.tuesdayYN = this.stringToBoolean(this.transferAuthorizationHour.tuesdayYN)
      this.tempDesignatedHour.transferDay.wednesdayYN = this.stringToBoolean(this.transferAuthorizationHour.wednesdayYN)
      this.tempDesignatedHour.transferDay.thursdayYN = this.stringToBoolean(this.transferAuthorizationHour.thursdayYN)
      this.tempDesignatedHour.transferDay.fridayYN = this.stringToBoolean(this.transferAuthorizationHour.fridayYN)
      this.tempDesignatedHour.transferDay.saturdayYN = this.stringToBoolean(this.transferAuthorizationHour.saturdayYN)
      this.tempDesignatedHour.transferDay.sundayYN = this.stringToBoolean(this.transferAuthorizationHour.sundayYN)

     }else if(this.approvalPayload){
        if(this.currentTab == 'CIB201003SS10'){
          let authDetailParsed = JSON.parse(this.approvalPayload?.requestContent);
          this.transferAuthorizationHour.operationServiceCode = authDetailParsed.operationServiceCode;
          this.designatedHour.startTime = this.$datetime.convertTimeStringToTimeObject(authDetailParsed.operationServiceStartTime);
          this.designatedHour.endTime = this.$datetime.convertTimeStringToTimeObject(authDetailParsed.operationServiceEndTime);
          this.designatedHour.transferDay.mondayYN = this.stringToBoolean(authDetailParsed.operationMondayYN)
          this.designatedHour.transferDay.tuesdayYN = this.stringToBoolean(authDetailParsed.operationTuesdayYN)
          this.designatedHour.transferDay.wednesdayYN = this.stringToBoolean(authDetailParsed.operationWednesdayYN)
          this.designatedHour.transferDay.thursdayYN = this.stringToBoolean(authDetailParsed.operationThursdayYN)
          this.designatedHour.transferDay.fridayYN = this.stringToBoolean(authDetailParsed.operationFridayYN)
          this.designatedHour.transferDay.saturdayYN = this.stringToBoolean(authDetailParsed.operationSaturdayYN)
          this.designatedHour.transferDay.sundayYN = this.stringToBoolean(authDetailParsed.operationSundayYN)

          this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
        }
     }
    },

    isEditable(isAllowTabChange) {
      this.$emit('isBeingEdited', isAllowTabChange)
    },

    'designatedHour.transferDay.mondayYN'(newValue){
      if(newValue == false){
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorizationHour.operationServiceCode = '03'
          if(this.isSelectAllDate == true){
            this.isSelectAllDate = false
          }
      }else{
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },
    
    'designatedHour.transferDay.tuesdayYN'(newValue){
      if(newValue == false){
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorizationHour.operationServiceCode = '03'
        if(this.isSelectAllDate == true){
            this.isSelectAllDate = false
          }
      }else{
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    'designatedHour.transferDay.wednesdayYN'(newValue){
      if(newValue == false){
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorizationHour.operationServiceCode = '03'
          if(this.isSelectAllDate == true){
            this.isSelectAllDate = false;
          }
      }else{
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    'designatedHour.transferDay.thursdayYN'(newValue){
      if(newValue == false){
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true;
        this.transferAuthorizationHour.operationServiceCode = '03';
        if(this.isSelectAllDate == true && this.isSecondTransferAuthorization == '02'){
            this.isChangeTransferDay = true;
            this.isSelectAllDate = false;
        }else{
          this.isSelectAllDate = false;
        }
      }else{
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    'designatedHour.transferDay.fridayYN'(newValue){
      if(newValue == false){
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorizationHour.operationServiceCode = '03'
        if(this.isSelectAllDate == true){
            this.isSelectAllDate = false
          }
      }else{
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    'designatedHour.transferDay.saturdayYN'(newValue){
      if(newValue == false){
        this.handleValidateTransferAuthorization(this.designatedHour)
        this.isChangeTransferDay = true
        this.transferAuthorizationHour.operationServiceCode = '03'
        if(this.isSelectAllDate == true){
            this.isSelectAllDate = false
          }
      }else{
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    'designatedHour.transferDay.sundayYN'(newValue){
      if(this.transferAuthorizationHour.operationServiceCode == '01'){
        if(newValue == true){
          this.isChangeTransferDay = false
        }else{
          this.isChangeTransferDay = true
          this.transferAuthorizationHour.operationServiceCode = '03'
          if(this.isSelectAllDate == true){
            this.isSelectAllDate = false
          }
        }
      }else if (this.transferAuthorizationHour.operationServiceCode == '02'){
        if(newValue == false){
          this.isChangeTransferDay = false
          if(this.isSelectAllDate == true){
          this.isSelectAllDate = false
        }
        }else{
          this.isSecondTransferAuthorization = '02' 
          this.handleValidateTransferAuthorization(this.designatedHour)
          this.isChangeTransferDay = true
          this.isSelectAllDate = true
          this.transferAuthorizationHour.operationServiceCode = '03'
        }
      }else{
        this.isSelectAllDate = false
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    isSelectAllDate(newValue){
     if(newValue){
      if( this.transferAuthorizationHour.operationServiceCode == '01'){
        this.isfirstTransferAuthorization = '01'
      }else if( this.transferAuthorizationHour.operationServiceCode == '02'){
        this.isthirdTransferAuthorization=''
        this.isSecondTransferAuthorization = '02'
        if(newValue == true){
          this.transferAuthorizationHour.operationServiceCode = '03';
          this.isChangeTransferDay = true;
          this.designatedHour.transferDay.mondayYN= true,
          this.designatedHour.transferDay.tuesdayYN= true,
          this.designatedHour.transferDay.wednesdayYN= true,
          this.designatedHour.transferDay.thursdayYN= true,
          this.designatedHour.transferDay.fridayYN= true,
          this.designatedHour.transferDay.saturdayYN= true,
          this.designatedHour.transferDay.sundayYN= true
        }
      }else{
        if(this.isChangeFromStartTime){
          this.isthirdTransferAuthorization=''
          this.isChangeTransferDay = true;
          this.designatedHour.transferDay.mondayYN= true,
          this.designatedHour.transferDay.tuesdayYN= true,
          this.designatedHour.transferDay.wednesdayYN= true,
          this.designatedHour.transferDay.thursdayYN= true,
          this.designatedHour.transferDay.fridayYN= true,
          this.designatedHour.transferDay.saturdayYN= true,
          this.designatedHour.transferDay.sundayYN= true
          // this.handleValidateTransferAuthorization(this.designatedHour)
          // this.transferAuthorizationHour.operationServiceCode = '01'
        }else{
          if(this.isfirstTransferAuthorization == '01'){
            const startimesun = '000000000';
            let startTimeConvert = '';
            startTimeConvert = this.$datetime.convertTimeObjectToTimeString(this.designatedHour.startTime)
            if((startTimeConvert == startimesun) && (this.designatedHour.endTime.hours == 23 && this.designatedHour.endTime.minutes == 59 && this.designatedHour.endTime.seconds == 0) && this.isSelectAllDate){
              this.transferAuthorizationHour.operationServiceCode = '01';
            }
            this.isChangeTransferDay = true;
            this.designatedHour.transferDay.mondayYN= true,
            this.designatedHour.transferDay.tuesdayYN= true,
            this.designatedHour.transferDay.wednesdayYN= true,
            this.designatedHour.transferDay.thursdayYN= true,
            this.designatedHour.transferDay.fridayYN= true,
            this.designatedHour.transferDay.saturdayYN= true,
            this.designatedHour.transferDay.sundayYN= true
          }else if (this.isSecondTransferAuthorization == '02'){
            this.isChangeTransferDay = true;
            this.designatedHour.transferDay.mondayYN= true,
            this.designatedHour.transferDay.tuesdayYN= true,
            this.designatedHour.transferDay.wednesdayYN= true,
            this.designatedHour.transferDay.thursdayYN= true,
            this.designatedHour.transferDay.fridayYN= true,
            this.designatedHour.transferDay.saturdayYN= true,
            this.designatedHour.transferDay.sundayYN= true
          }else if(this.isthirdTransferAuthorization == '03'){
            this.isChangeTransferDay = true;
            this.isDisableTransferDays = false
            this.designatedHour.transferDay.mondayYN= true,
            this.designatedHour.transferDay.tuesdayYN= true,
            this.designatedHour.transferDay.wednesdayYN= true,
            this.designatedHour.transferDay.thursdayYN= true,
            this.designatedHour.transferDay.fridayYN= true,
            this.designatedHour.transferDay.saturdayYN= true,
            this.designatedHour.transferDay.sundayYN= true
          }
          // this.handleValidateTransferAuthorization(this.designatedHour)
          // if(this.isthirdTransferAuthorization=='03'){
          //   this.isDisableTransferDays = false
          // }
        }
        // if(this.isthirdTransferAuthorization=='03'){
        //     this.isDisableTransferDays = false
        //   }
      }
     }else{
      if( this.transferAuthorizationHour.operationServiceCode == '01'){
        if( this.isfirstTransferAuthorization == '01'){
            if( this.designatedHour.transferDay.mondayYN== true &&
            this.designatedHour.transferDay.tuesdayYN== true &&
            this.designatedHour.transferDay.wednesdayYN== true &&
            this.designatedHour.transferDay.thursdayYN== true &&
            this.designatedHour.transferDay.fridayYN== true &&
            this.designatedHour.transferDay.saturdayYN== true&&
            this.designatedHour.transferDay.sundayYN== true){
              this.designatedHour.transferDay.mondayYN= false,
              this.designatedHour.transferDay.tuesdayYN= false,
              this.designatedHour.transferDay.wednesdayYN= false,
              this.designatedHour.transferDay.thursdayYN= false,
              this.designatedHour.transferDay.fridayYN= false,
              this.designatedHour.transferDay.saturdayYN= false,
              this.designatedHour.transferDay.sundayYN= false
            }
          }
      }else if( this.transferAuthorizationHour.operationServiceCode == '02'){
        // this is operationServiceCode
      }else{
        if(this.isSecondTransferAuthorization == '02'){
          if( this.designatedHour.transferDay.mondayYN== true &&
            this.designatedHour.transferDay.tuesdayYN== true &&
            this.designatedHour.transferDay.wednesdayYN== true &&
            this.designatedHour.transferDay.thursdayYN== true &&
            this.designatedHour.transferDay.fridayYN== true &&
            this.designatedHour.transferDay.saturdayYN== true&&
            this.designatedHour.transferDay.sundayYN== true){
              this.designatedHour.transferDay.mondayYN= false,
              this.designatedHour.transferDay.tuesdayYN= false,
              this.designatedHour.transferDay.wednesdayYN= false,
              this.designatedHour.transferDay.thursdayYN= false,
              this.designatedHour.transferDay.fridayYN= false,
              this.designatedHour.transferDay.saturdayYN= false,
              this.designatedHour.transferDay.sundayYN= false
          }
        }else{
          this.transferAuthorizationCode == '02' ?  this.isthirdTransferAuthorization = '' : this.isthirdTransferAuthorization = '03';
          if( this.designatedHour.transferDay.mondayYN== true &&
            this.designatedHour.transferDay.tuesdayYN== true &&
            this.designatedHour.transferDay.wednesdayYN== true &&
            this.designatedHour.transferDay.thursdayYN== true &&
            this.designatedHour.transferDay.fridayYN== true &&
            this.designatedHour.transferDay.saturdayYN== true&&
            this.designatedHour.transferDay.sundayYN== true){
              this.designatedHour.transferDay.mondayYN= false,
              this.designatedHour.transferDay.tuesdayYN= false,
              this.designatedHour.transferDay.wednesdayYN= false,
              this.designatedHour.transferDay.thursdayYN= false,
              this.designatedHour.transferDay.fridayYN= false,
              this.designatedHour.transferDay.saturdayYN= false,
              this.designatedHour.transferDay.sundayYN= false,
              this.isDisableTransferDays = true;
          }
          this.isChangeTransferDay = true;
        }
      } 
     }
    }
  },

  methods: {
    handleStartTime(transferAuthorizationCode, transferAuthorizationData){
      if(transferAuthorizationCode == '02'){
        if(transferAuthorizationData.hours != 8 || transferAuthorizationData.minutes != 0 || transferAuthorizationData.seconds != 0){
          this.transferAuthorizationCode = '02'
          this.transferAuthorizationHour.operationServiceCode = '03'
          this.isChangeFromStartTime = true;
          this.isDisableTransferDays = true;
        }else{
          this.isChangeFromStartTime = false;
        }
      }else if(transferAuthorizationCode == '01'){
        if(transferAuthorizationData.hours != 0 || transferAuthorizationData.minutes != 0 || transferAuthorizationData.seconds != 0){
          this.transferAuthorizationCode = '';
          this.transferAuthorizationHour.operationServiceCode = '03';
          this.isChangeFromStartTime = true;
          this.isDisableTransferDays = true;
        }else{
          this.isChangeFromStartTime = false;
        }
      }else{
        const startimesun = '000000000';
        let startTimeConvert = '';
         startTimeConvert = this.$datetime.convertTimeObjectToTimeString(this.designatedHour.startTime)
        if((startTimeConvert == startimesun) && (this.designatedHour.endTime.hours == 23 && this.designatedHour.endTime.minutes == 59 && this.designatedHour.endTime.seconds == 0) && this.designatedHour.transferDay.mondayYN && this.designatedHour.transferDay.tuesdayYN && this.designatedHour.transferDay.wednesdayYN && this.designatedHour.transferDay.thursdayYN && this.designatedHour.transferDay.fridayYN && this.designatedHour.transferDay.saturdayYN && this.designatedHour.transferDay.sundayYN){
          this.transferAuthorizationHour.operationServiceCode = '01';
        }
        this.isChangeFromStartTime = false
        this.handleValidateTransferAuthorization(this.designatedHour)
      }
    },

    changefromendTime(transferAuthorizationCode, transferAuthorizationData){
      if(transferAuthorizationCode == '01'){
        if(transferAuthorizationData.hours != 23 || transferAuthorizationData.minutes != 59 || transferAuthorizationData.seconds != 59){
          this.transferAuthorizationHour.operationServiceCode = '03'
          this.transferAuthorizationCode = '01'
          this.isChangeFromStartTime = true
          this.isDisableTransferDays = true
        }
        else{
          this.handleValidateTransferAuthorization(this.designatedHour)
        }
      }else if(transferAuthorizationCode == '02'){
        if(transferAuthorizationData.hours != 18 || transferAuthorizationData.minutes != 0 || transferAuthorizationData.seconds != 0){
          this.transferAuthorizationCode = '02'
          this.transferAuthorizationHour.operationServiceCode = '03'
          this.isChangeFromStartTime = true
          this.isDisableTransferDays = true
        }
      }else{
        const startimesat =  '080000000';
        const startimesun =  '000000000';
        this.isthirdTransferAuthorization == '03' ? this.isDisableTransferDays = false : this.isDisableTransferDays = true;
        let startTimeConvert = ''
        if(this.isthirdTransferAuthorization == '03' && this.isSelectAllDate == false){
          this.isDisableTransferDays = true
        }else if (this.isthirdTransferAuthorization == '03' && this.isSelectAllDate == true){
          this.isDisableTransferDays = false
        }else{
          this.isDisableTransferDays = true
        }
        startTimeConvert = this.$datetime.convertTimeObjectToTimeString(this.designatedHour.startTime)
        if((startTimeConvert == startimesun) && (this.designatedHour.endTime.hours == 23 && this.designatedHour.endTime.minutes == 59 && this.designatedHour.endTime.seconds == 0) && this.designatedHour.transferDay.mondayYN && this.designatedHour.transferDay.tuesdayYN && this.designatedHour.transferDay.wednesdayYN && this.designatedHour.transferDay.thursdayYN && this.designatedHour.transferDay.fridayYN && this.designatedHour.transferDay.saturdayYN && this.designatedHour.transferDay.sundayYN){
          this.transferAuthorizationHour.operationServiceCode = '01';
        }else if ((startTimeConvert == startimesat) && (this.designatedHour.endTime.hours == 18 && this.designatedHour.endTime.minutes == 0 && this.designatedHour.endTime.seconds == 0) && this.designatedHour.transferDay.mondayYN && this.designatedHour.transferDay.tuesdayYN && this.designatedHour.transferDay.wednesdayYN && this.designatedHour.transferDay.thursdayYN && this.designatedHour.transferDay.fridayYN && this.designatedHour.transferDay.saturdayYN && !this.designatedHour.transferDay.sundayYN){
          this.transferAuthorizationHour.operationServiceCode = '02';
        }
      }
    },

    onClickOperation(btn: string) {
      switch (btn) {
        case "edit":
          this.isEditable = true;
          this.isDisable = false;
          if(this.transferAuthorizationHour.operationServiceCode == '03'){
            this.isAllowDesignatedHour = true;
          }else{
            this.isAllowDesignatedHour = false;
          }
          break;

        case "cancel":
          this.transferAuthorizationHour.operationServiceCode = this.tempOperationCode
          this.designatedHour.startTime =  this.tempDesignatedHour.startTime
          this.designatedHour.endTime =   this.tempDesignatedHour.endTime 
          this.designatedHour.transferDay.mondayYN =  this.tempDesignatedHour.transferDay.mondayYN 
          this.designatedHour.transferDay.tuesdayYN = this.tempDesignatedHour.transferDay.tuesdayYN
          this.designatedHour.transferDay.wednesdayYN =  this.tempDesignatedHour.transferDay.wednesdayYN
          this.designatedHour.transferDay.thursdayYN =  this.tempDesignatedHour.transferDay.thursdayYN
          this.designatedHour.transferDay.fridayYN = this.tempDesignatedHour.transferDay.fridayYN
          this.designatedHour.transferDay.saturdayYN = this.tempDesignatedHour.transferDay.saturdayYN
          this.designatedHour.transferDay.sundayYN =  this.tempDesignatedHour.transferDay.sundayYN
          this.isEditable = false;
          this.isDisableTransferDays = false;
          this.isAllowDesignatedHour = false;
          break;

        case "list":
          this.$router.push('/corporate-internet-banking/subscription');
          break;

        case "submit": 
          this.$dialog.onOpen({
            content: `${this.$t("message.would_you_like_to_save")}`,
            dialogMode:"Question",
            callback:()=>{
              this.handleSubmit();
              modalController.dismiss();
            }
          });
          break;
      }
    },

    handleTransferHourFromMonToSun(){
      this.designatedHour = {
        startTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
        endTime: this.$datetime.convertTimeStringToTimeObject('235959999'),
        transferDay: {
          mondayYN: true,
          tuesdayYN: true,
          wednesdayYN: true,
          thursdayYN: true,
          fridayYN: true,
          saturdayYN: true,
          sundayYN: true
        }
      }
    },

    handleTransferHourFromMonToSat(){
      this.isSelectAllDate = false
      this.designatedHour = {
        startTime: this.$datetime.convertTimeStringToTimeObject('080000000'),
        endTime: this.$datetime.convertTimeStringToTimeObject('180000000'),
        transferDay: {
          mondayYN: true,
          tuesdayYN: true,
          wednesdayYN: true,
          thursdayYN: true,
          fridayYN: true,
          saturdayYN: true,
          sundayYN: false
        }
      }
    },

    handleValidateTransferAuthorization(transferAuthorizationData){
      const startimesun =  '000000000';
      const endtimesun =  '235959000';
      const startimesat =  '080000000';
      const endtimesat =  '180000000';
   
      const startTimeConvert = this.$datetime.convertTimeObjectToTimeString(transferAuthorizationData.startTime)
      const endTimeConvert = this.$datetime.convertTimeObjectToTimeString(transferAuthorizationData.endTime)
      if(transferAuthorizationData.transferDay.mondayYN && transferAuthorizationData.transferDay.tuesdayYN && transferAuthorizationData.transferDay.wednesdayYN&&transferAuthorizationData.transferDay.thursdayYN && transferAuthorizationData.transferDay.fridayYN && transferAuthorizationData.transferDay.saturdayYN && transferAuthorizationData.transferDay.sundayYN){
        this.isSelectAllDate =true;
      }
      if((startTimeConvert == startimesun) && (endTimeConvert == endtimesun || endTimeConvert == '235959999') && transferAuthorizationData.transferDay.mondayYN && transferAuthorizationData.transferDay.tuesdayYN && transferAuthorizationData.transferDay.wednesdayYN && transferAuthorizationData.transferDay.thursdayYN && transferAuthorizationData.transferDay.fridayYN && transferAuthorizationData.transferDay.saturdayYN && transferAuthorizationData.transferDay.sundayYN){
        this.transferAuthorizationHour.operationServiceCode = '01';
      }else if(startTimeConvert == startimesat &&  endTimeConvert==endtimesat && transferAuthorizationData.transferDay.mondayYN && transferAuthorizationData.transferDay.tuesdayYN&&transferAuthorizationData.transferDay.wednesdayYN && transferAuthorizationData.transferDay.thursdayYN && transferAuthorizationData.transferDay.fridayYN && transferAuthorizationData.transferDay.saturdayYN && !transferAuthorizationData.transferDay.sundayYN){
        this.transferAuthorizationHour.operationServiceCode = '02';
      }
    },

    handleSelectionChange(){
      if(this.transferAuthorizationHour.operationServiceCode == '01'){
        this.handleTransferHourFromMonToSun()
        this.isfirstTransferAuthorization = '01'
        this.isSecondTransferAuthorization = '';
        this.isthirdTransferAuthorization = '';
        this.isChangeTransferDay = false;
        this.isSelectAll = '';
        this.isDisableTransferDays = true;
        this.isSelectAllDate = true;
        this.isChangeFromStartTime = false;

      }else if(this.transferAuthorizationHour.operationServiceCode == '02'){
        this.handleTransferHourFromMonToSat()
        this.isfirstTransferAuthorization = ''
        this.isSecondTransferAuthorization = '02'
        this.isthirdTransferAuthorization ='';
        this.isSelectAll = '';
        this.isDisableTransferDays = true;
        this.isChangeTransferDay = false;
        this.isChangeFromStartTime = false;
      }else if(this.transferAuthorizationHour.operationServiceCode == '03'){
        this.isSelectAll = ''
        if(this.isChangeTransferDay == true){
          return this.designatedHour
        }else  if(this.isChangeFromStartTime){
          if(this.transferAuthorizationCode == '02' || this.transferAuthorizationCode == '03'){
            this.isSelectAllDate = false;
          }else{
            this.handleValidateTransferAuthorization(this.designatedHour)
            this.isSelectAllDate = true;
          }
        }else{
          this.designatedHour = {
            startTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
            endTime: this.$datetime.convertTimeStringToTimeObject('000000000'),
            transferDay: {
              mondayYN: false,
              tuesdayYN: false,
              wednesdayYN: false,
              thursdayYN: false,
              fridayYN: false,
              saturdayYN: false,
              sundayYN: false
            }
          }
          this.isthirdTransferAuthorization = '03';
          this.transferAuthorizationCode = '';
          this.isfirstTransferAuthorization = ''
          this.isSecondTransferAuthorization = '';
        }
      } 
    },

    handleDisableAuthorizationHour(operationServiceCode){
      if(operationServiceCode == '01' || operationServiceCode == '02'){
        return !this.isAllowDesignatedHour;
      }else if(operationServiceCode == '03'){
        if(this.isEditable == false){
          return this.isDisable = true;
        }else{
          return this.isDisable = false;
        }
      }
    },

    async handleSubmit() {
      let body: CIB20100339_REQ_BODY= {
        userID: `${this.userID}`,
        customerNo: `${this.customerNo}`,
        operationServiceCode: this.transferAuthorizationHour.operationServiceCode,
        operationServiceStartTime:  this.$datetime.convertTimeObjectToTimeString(this.designatedHour.startTime),
        operationServiceEndTime:  this.$datetime.convertTimeObjectToTimeString(this.designatedHour.endTime),
        operationMondayYN: this.booleanToString(this.designatedHour.transferDay.mondayYN),
        operationTuesdayYN: this.booleanToString(this.designatedHour.transferDay.tuesdayYN),
        operationWednesdayYN: this.booleanToString(this.designatedHour.transferDay.wednesdayYN),
        operationThursdayYN: this.booleanToString(this.designatedHour.transferDay.thursdayYN),
        operationFridayYN: this.booleanToString(this.designatedHour.transferDay.fridayYN),
        operationSaturdayYN: this.booleanToString(this.designatedHour.transferDay.saturdayYN),
        operationSundayYN: this.booleanToString(this.designatedHour.transferDay.sundayYN)
      }

      const responseData = await  await this.$request.getRequest('CIB20100339', body, false, 'CIB201003SS10') as CIB20100339_RES;
      if(this.approvalType){
        this.isEditable = false;
        this.isDisableTransferDays = false;
        this.isAllowDesignatedHour = false;
        this.$emit("isReseTButton", false);
      }
      
      if (responseData.header.result) {
        this.isEditable = false;
        this.isDisableTransferDays = false;
        this.isAllowDesignatedHour = false;
        this.$emit("isReseTButton", false);
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode:"Success",
          callback:()=>{
            modalController.dismiss()
          }
        })
      }
    },

    timeFormatter(time: string) {
      const tempTime = time.split(":")
      return { "hours": Number(tempTime[0]), "minutes": Number(tempTime[1]), "seconds": 0 }
    },

    stringToBoolean(YN: string){
      return YN === 'Y' ?  true:  false
    },

    booleanToString(YN: boolean){
      return YN ? "Y" : "N" 
    }
  }
})

